.simpleLink {
  transition: all 0.2s;
  font-size: 0.875em;
  font-weight: 700;
  color: #50263a;
}

.inlineBlock {
  display: inline-block;
  text-align: center;
  padding: 1em;
  background-color: #fccc51;
  border-radius: 5px;
  text-decoration: none;
  box-shadow: 2px 4px 6px rgba(37, 48, 149, 0.25);
  cursor: pointer;
  box-sizing: border-box;
}
.inlineBlock:focus, .inlineBlock:hover, .inlineBlock:active {
  background-color: #eabe4f;
}

.inline {
  text-decoration: underline;
  cursor: pointer;
}
.inline:hover {
  text-decoration: none;
}